$text-font: 13px Arial, sans-serif;
$header-font: 'Ubuntu', sans-serif;

$base-color: #f4b60d;
$second-color: #248cec;
$background-color: #fff;
$section-color: #2a2d32;
$text-color: #8a8a8a;
$header-color: #2a2b2e;

$wrapper: 1240px;


@mixin size($size) {
  width: $size;
  height: $size; }

@mixin round($radius: 50%) {
  border-radius: $radius; }

@mixin bg-center($url) {
  background: url($url) center center no-repeat; }

@mixin hide-text() {
  text-indent: -1000px;
  overflow: hidden; }

@mixin crop-text() {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }

@mixin placeholder {
  &::-webkit-input-placeholder {
    @content; }
  &:-moz-placeholder {
    @content; }
  &::-moz-placeholder {
    @content; }
  &:-ms-input-placeholder {
    @content; } }

%centered {
  width: $wrapper;
  margin: 0 auto;
  padding: 0 40px; }
